.city-selector {
    text-align: center;
    margin-top: 50px;
    font-family: Arial, sans-serif;
  }
  
  .dropdowns {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .dropdown {
    padding: 10px;
    font-size: 16px;
  }
  
  .result {
    margin-top: 20px;
    font-size: 18px;
  }
  
  .highlight {
    font-weight: bold;
    font-size: 24px;
  }
  
  .fade {
    color: #888;
  }
  